.App{
  background: #f7f7f7;
}

.App section{
  margin-top: 0;
  margin-bottom: 16px;
}


.App-header{
  text-align: center;
}
.App-logo {

}

.App-header {
  background: #343150;
  color: white;
}

.App-footer{
  background: #343150;
  color: white;
  text-align: center;
  font-size: 12px;
  padding: 20px 0;
}

.App-title {
  font-size: 3em;
  position: absolute;
  -webkit-transform: translate(-50%,0);
      -ms-transform: translate(-50%,0);
          transform: translate(-50%,0);
  left: 50%;
  top:150px;
}

.App-title:after{
  display: block;
  content:'Welcome to EUI';
  font-size: 1em;
  opacity: 0.3;
  position: absolute;
  -webkit-transform:translate(10px,-20px) scaleY(-1) skew(-30deg);
      -ms-transform:translate(10px,-20px) scaleY(-1) skew(-30deg);
          transform:translate(10px,-20px) scaleY(-1) skew(-30deg);
}

.App-intro {
  font-size: large;
}

.App .report{
  width: 100%;
  min-height: 500px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@font-face {
  font-family: octicons-link;
  src: url(data:font/woff;charset=utf-8;base64,) format('woff');
}

.App-content{
  width: 800px;
  margin: 20px auto;
  padding: 40px;
  background: #fff;
  position: relative;
}

.App-content a{
  text-decoration: none;
  color:#333;
}

.App-content .active{
  color:#3B99FF;
}

.App-menu{
  position: absolute;
  left: -10px;
  top:0;
  width: 240px;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
  background: #fff;
  line-height: 2;
  border-radius: 4px;
  overflow: hidden;
}
.App-menu__title{
  text-align: center;
  color:#fff;
  background: #343150;
}
.App-menu__list{
  padding: 15px;
}

.App-menu__hr{
  background:#ccc;
  height: 1px;
  width: 100%;
  margin: 5px 0;
}



